/* GRID START */

.grid2{
    display: grid;
    grid-template-columns: repeat(2, 1fr); /* 2 columns */
    justify-content: center;
    align-items: center;
  }
  
  
  .gap1 {
    gap: 0.5rem; /* Adjust the gap between columns */
  }
  .gap2 {
    gap: 1rem; /* Adjust the gap between columns */
  }
  .gap3 {
    gap: 1.5rem; /* Adjust the gap between columns */
  }

.container{
  width:100%;
  margin-left: auto;
  margin-right:auto;
}
.container-fluid{
  width:100%;
  padding:15px 0;
  margin-left: auto;
  margin-right:auto;
}
.row {
display:flex;
flex-flow:row wrap;
width:100%;
height: 100%;
}

.flex-wrap{
    flex-wrap: wrap;
}
.flex-nowrap{
    flex-wrap: nowrap;
}

.col-1,.col-2,.col-3,.col-4,.col-5,.col-6,.col-7,.col-8,.col-9,.col-10,.col-11,.col-12{
width:100%;
height: 100%;
max-width: 100%;
/* padding-left: 15px;
padding-right: 15%; */
}

.row.col-1{
flex : 0 0 calc(100%/12);
}
.row .col-2{
flex : 0 0 calc(100%/6);
}
.row .col-3{
flex : 0 0 calc(100%/4);
}
.row .col-4{
flex : 0 0 calc(100%/3);
}
.row .col-5{
flex : 0 0 calc(100%/2.4);
}
.row .col-6{
flex : 0 0 calc(100%/2);
}
.row .col-7{
flex : 0 0 calc(100%/1.714285);
}
.row .col-8{
flex : 0 0 calc(100%/1.5);
}
.row .col-9{
flex : 0 0 calc(100%/1.333333);
}
.row .col-10{
flex : 0 0 calc(100%/1.2);
}
.row .col-11{
flex : 0 0 calc(100%/1.090909);
}
.row .col-12{
flex : 0 0 calc(100%/1);
}
